* {
  margin: 0;
  padding: 0;
}
.container {
  max-width: 1920px;
  padding: 20px;
  margin: 0 auto;
}
.search-bar {
  border: none;
  outline: none;
  width: 600px;
}
::-webkit-scrollbar {
  width: 0;
  height: 5px;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(114 113 113);
  border-radius: 10px;
  height: 200px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

a {
  text-decoration: none;
  color: #000;
}
.category-btn {
  color: #fff !important;
  text-transform: capitalize;
  display: flex;
  align-items: center;
  cursor: pointer;
  background-color: transparent;
  outline: none;
  border: none;
  padding: 7px 15px;
  margin: 10px 0;
  border-radius: 20px;
  transition: all 0.5s ease;
}
.category-btn:hover {
  background-color: #ff0000 !important;
  color: #000;
  transition: all 0.5s ease;
}
.category-btn:hover span {
  color: #fff !important;
  transition: all 0.5s ease;
}
